import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Input } from '@angular/core';
import { Router } from '@angular/router';
import { Users } from '../objects/user';
import { UserRole } from '../objects/user-role';
import { ReturnObject } from '../objects/returnObject'
import { LoginService } from '../services/login.service';
import { AuthService } from '../services/auth.service';
import { PermissionService } from '../services/permission.service';
import { NgxPermissionsService } from 'ngx-permissions';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  private formSubmitAttempt: boolean; // {2}
  @Input() user : Users;
  userRole : UserRole;
  loginReturnObject : ReturnObject;
  constructor(private ls : LoginService,
              private authService: AuthService,
              private ps : PermissionService,
              private permissionsService: NgxPermissionsService) { }

  ngOnInit() {
    this.userRole = new UserRole();
    this.user = new Users();
  }

  loginUser(e : any){
    e.preventDefault();
    this.formSubmitAttempt = true;
    this.ls.loginUser(this.user).then(returnObj => this.evaluateReturn(returnObj));
    //this.ls.login(this.user).subscribe(response => console.log(response));
  }

  evaluateReturn(returnObj : any){
    this.loginReturnObject = returnObj;//sdsasdas
    if(this.loginReturnObject.status=='SUCCESS'){
       this.authService.login();

       let user = (JSON.parse(returnObj.returnedObject) as Users[])[0];
       let userRole = this.userRole;
       userRole.roleId = user.userRoleId;
       this.ps.loadPermission(userRole).subscribe(userRole => this.loadPermissions(userRole));
    }
  }

  loadPermissions(userRole : UserRole){
    this.permissionsService.addPermission(userRole.roleType);
    //var permissions = this.permissionsService.getPermissions();
    //console.log(permissions);
  }

}



