import { Injectable } from '@angular/core';
import { Headers, Http,Response } from '@angular/http';
import { ReturnObject } from '../objects/returnObject';
import { Users } from '../objects/user'
import { Observable } from 'rxjs';

@Injectable()
export class LoginService {
  public token: string;
	constructor(private http: Http) {
	  var currentUser = JSON.parse(localStorage.getItem('currentUser'));
    this.token = currentUser && currentUser.token;
  }

	loginUser(user : Users) : Promise<ReturnObject>{
	  console.log('/api/loginUser2222');
    return this.http.post('/api/loginUser',user)
      .toPromise()
      .then(response => response.json() as ReturnObject)
      .catch(this.handleError);
  }

  login(user : Users): Observable<boolean> {
      return this.http.post('/api/loginUser', user)
          .map((response: Response) => {
              // login successful if there's a jwt token in the response
              let token = response.json() && response.json().token;
              if (token) {
                  // set token property
                  this.token = token;

                  // store username and jwt token in local storage to keep user logged in between page refreshes
                  localStorage.setItem('currentUser', JSON.stringify({ username: user.userName, token: token }));

                  // return true to indicate successful login
                  return true;
              } else {
                  // return false to indicate failed login
                  return false;
              }
          });
  }

  logout(){
    this.token = null;
    localStorage.removeItem('currentUser');
  }

  private handleError(error: any): Promise<any> {
    console.error('An error    occurred', error);
    return Promise.reject(error.message || error);
  }

}



