import { Injectable } from '@angular/core';
import { Headers, Http,Response } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import { catchError, map, tap } from 'rxjs/operators';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { UserRole } from '../objects/user-role'
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class PermissionService {
  private permittedRole = new BehaviorSubject<string>('');
  constructor(private http : Http) { }

  getPermission() {
      return this.permittedRole.getValue();
    }

  loadPermission(userRole : UserRole):Observable<UserRole>{
    return this.http.post('http://localhost:8080/api/loadPermissions',userRole)
      .map((res : Response) =>{
        let resp = res.json() as UserRole;
        this.permittedRole.next(resp.roleType);
        console.log(resp);
        return resp;
      });
  }

  private handleError(error: any): Promise<any> {
    console.error('An error    occurred', error);
    return Promise.reject(error.message || error);
  }

}



