import { Component, OnInit, Input } from '@angular/core';
import { Test } from '../test'
import { Device } from '../device'
import { Policy } from '../objects/policy'
import { ReturnObject } from '../objects/returnObject'
import { Observable } from 'rxjs/Observable';
import { TestingService } from '../testing.service'


@Component({
  selector: 'app-angular-springboot',
  templateUrl: './angular-springboot.component.html',
  styleUrls: ['./angular-springboot.component.css']
})
export class AngularSpringbootComponent implements OnInit {
  testData : Test[];
  devices : Device[];
  noNo : any[];
  retrievedPolicy : ReturnObject;
  @Input() test : Test;
  @Input() policy : Policy;

  constructor(private testingService: TestingService) { }

  ngOnInit() {
    this.policy = new Policy();
    //this.test = new Test('');
    //this.saveInsured();
    //this.getAllCumulocityDevices();
    //this.getAngularSpringBoot();
    //this.testCumulocity();
  }

  findInsured(){
     this.testingService.findInsured(this.policy).then(retrievedPolicy => this.retrievedPolicy = retrievedPolicy[0]);
  }

  saveInsured(){
      this.testingService.saveInsured(this.policy).then(noNo => this.noNo = noNo);
  }

  testCumulocity(){
    this.testingService.testCumulocity(this.test).then(test => this.test = test[0]);
  }

  getAllCumulocityDevices(){
    console.log('getting devices');
    this.testingService.getAllCumulocityDevices().then(devices => this.devices = devices);
    console.log('devices gotten');
  }
}



