import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { HttpInterceptor, HttpHandler, HttpRequest,HTTP_INTERCEPTORS } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { NgxPermissionsModule } from 'ngx-permissions';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './/app-routing.module';
import { AngularSpringbootComponent } from './angular-springboot/angular-springboot.component';
import { TestingService } from './testing.service';
import { GmapsComponent } from './gmaps/gmaps.component';
import { RequestComponent } from './request/request.component';
import { GplacesDirective } from './gplaces.directive';
import { TowtrucksService } from './services/towtrucks.service';
import { DeviceService } from './services/device.service';
import { LoginComponent } from './login/login.component';
import { AngularFontAwesomeModule } from 'angular-font-awesome';
import { RequestService } from './services/request.service';
import { LoginService } from './services/login.service';
import { AuthService } from './services/auth.service';
import { AuthGuard } from './guards/auth.guard';
import { PermissionService } from './services/permission.service';

@NgModule({

  declarations: [
    AppComponent,
    AngularSpringbootComponent,
    GmapsComponent,
    RequestComponent,
    GplacesDirective,
    LoginComponent
  ],
  imports: [
    FormsModule,
    BrowserModule,
    AppRoutingModule,
    HttpModule,
    HttpClientModule,
    AngularFontAwesomeModule,
    NgxPermissionsModule.forRoot()
  ],
  providers: [TestingService,
              TowtrucksService,
              DeviceService,
              RequestService,
              LoginService,
              AuthService,
              AuthGuard,
              PermissionService],
  bootstrap: [AppComponent]

})

export class AppModule { }



