import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import { Test } from './test'
import { Policy } from './objects/policy'
import { ReturnObject } from './objects/returnObject'
import { Device } from './device'
import { Distance } from './distance'
import { catchError, map, tap } from 'rxjs/operators';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';

@Injectable()
export class TestingService {
    private headers = new Headers({ 'Content-Type': 'application/json' });
    constructor(private http: Http) { }

    testCumulocity(test : Test): Promise<Test[]> {
      return this.http.post('http://localhost:8080/api/testCumulocity', test)
        .toPromise()
        .then(response => response.json() as Test[])
        .catch(this.handleError);
    }

    getAllCumulocityDevices() : Promise<Device[]> {
      return this.http.get('http://localhost:8080/api/getAllCumulocityDevices')
              .toPromise()
              .then(response => response.json() as Device[])
              .catch(this.handleError);
    }

    saveInsured(policy : Policy) : Promise<Policy[]>{
      return this.http.put('http://localhost:8080/api/saveInsured',policy)
        .toPromise()
        .then(response => response.json() as Policy[])
        .catch(this.handleError);
    }

    findInsured(policy : Policy) : Promise<ReturnObject[]> {
      return this.http.put('http://localhost:8080/api/findInsured',policy)
          .toPromise()
          .then(response => response.json() as ReturnObject[])
          .catch(this.handleError);
    }

    private handleError(error: any): Promise<any> {
      console.error('An error    occurred', error);
      return Promise.reject(error.message || error);
    }
}



