import { Component, OnInit } from '@angular/core';
import { RequestService } from '../services/request.service';
import { ViewChild,Directive, Input } from '@angular/core';
import { Request } from '../objects/request'
import { ReturnObject } from '../objects/returnObject'

@Component({
  selector: 'app-request',
  templateUrl: './request.component.html',
  styleUrls: ['./request.component.css']
})
export class RequestComponent implements OnInit {

  @Input() request : Request;
  returnObject : ReturnObject;
  submitted : boolean;

  constructor(private rService : RequestService) { }

  ngOnInit() {
    this.request = new Request();
  }

  clear(){
    this.submitted = false;
    this.request = new Request();
    this.returnObject = null;
  }

  submitRequest(){
    this.submitted = true;
    this.request.status = 'REQUESTED';
    let timeRequested = new Date();
    let timestamp = timeRequested.getHours() + ':' + timeRequested.getMinutes() + ':' + timeRequested.getSeconds();
    let formatted_date = timeRequested.getDate() + "/" + timeRequested.getMonth() + 1 + "/" + timeRequested.getFullYear()
    this.request.timeRequested = formatted_date+' '+ timestamp;
    this.rService.submitRequest(this.request).then(ro => this.evaluateResponse(ro));
  }

  evaluateResponse(value : any[]){
    this.returnObject = value[0];
    let requests = JSON.parse(value[0].returnedObject) as Request[];
    this.request = requests[0];
  }

}



